<template>
  <div class='container'>
    <router-link to="hy" tag="p" class="title">
        {{msg}}
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: '欢迎来到SaiHy的博客'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
  .container{
      position absolute
      left 0
      right 0
      top:0
      bottom 0
      text-align:center;
    font-size:20px;
      background url('../assets/bg1.jpg')
      animation bgmove 13s linear infinite alternate
  }
  img {
      max-width:600px;
      width:90%;
      margin :60px auto 20px;
      display:block;
    }
    .title{
        color:#fefefe;
        position relative
        animation flyIn 4s linear forwards ,scale 10s linear 4s infinite alternate
    }
    @keyframes bgmove{
        0%{
          background-position left
        }
        100%{
            background-position right
        }
    }
    @-webkit-keyframes bgmove{
        0%{
          background-position left
        }
        100%{
            background-position right
        }
    }
    @keyframes  flyIn{
        0%{
            top 0%
            opacity .2
        }
        10%{
            top 3%
            opacity .4
        }
        20%{
            top 7%
            opacity .6
        }
        30%{
            top 12%
            opacity .8
        }
        40%{
            top 18%
            opacity 1
        }
        50%{
            top 25%
            opacity .6
        }
        60%{
            top 33%
            opacity .8
        }
        70%{
            top 28%
            opacity 1
        }
        80%{
            top 33%
            opacity .8
        }
        90%{
            top 30%
            opacity .9
        }
        100%{
            top 33%
            opacity 1
        }
    }
    @-webkit-keyframes  flyIn{
        0%{
            top 0%
            opacity .2
        }
        10%{
            top 3%
            opacity .4
        }
        20%{
            top 7%
            opacity .6
        }
        30%{
            top 12%
            opacity .8
        }
        40%{
            top 18%
            opacity 1
        }
        50%{
            top 25%
            opacity .6
        }
        60%{
            top 33%
            opacity .8
        }
        70%{
            top 28%
            opacity 1
        }
        80%{
            top 33%
            opacity .8
        }
        90%{
            top 30%
            opacity .9
        }
        100%{
            top 33%
            opacity 1
        }
    }
    @keyframes scale {
        0%,100%{
            font-size 1em
            opacity 1
        }
        50%{
            font-size 1.2em
            opacity .6
        }
    }
    @-webkit-keyframes scale {
        0%,100%{
            font-size 1em
            opacity 1
        }
        50%{
            font-size 1.2em
            opacity .6
        }
    }
</style>



